.sidebar {
    width: 250px;
    height: 100vh;
    background-color: #222;
    color: white;
    position: fixed;
    padding: 20px;
  }
  
  .sidebar ul {
    list-style: none;
    padding: 0;
  }
  
  .sidebar ul li {
    padding: 10px 0;
  }
  
  .sidebar ul li a {
    color: white;
    text-decoration: double;
    font-size: 18px;
  }
  
  .sidebar ul li a:hover {
    text-decoration: underline;
    color: #45d6d6;
    font-size: 22px
  }
  .components h1 {
    font-size: 300px;
    font-weight: bold;
    color: #333;
  }
  
  .page p {
    font-size: 40px;
    line-height: 1.6;
    color: #555;
  }
  
  .ht{
    font-size: 35px;
  }
  .ht:hover{
    color:rgb(204, 131, 194);
    text-decoration: solid;
    
  }